import {
	apiBaseUrl
} from '@/config/config.js'
export default {
	props: {
		// 如果是商品订单此参数必须
		orderId: {
			type: String,
			default () {
				return ''
			}
		},
		// 如果是充值订单此参数必须
		recharge: {
			type: Number,
			default () {
				return 0
			}
		},
		// 用户id
		uid: {
			type: Number,
			default () {
				return 0
			}
		},
		// 订单类型
		type: {
			type: Number,
			default () {
				return 1
			}
		}
	},
	data() {
		return {
			payments: [],
			popShow: false
		}
	},
	mounted() {
		this.getPayments()
	},
	methods: {
		// 获取可用支付方式列表
		getPayments() {
			this.$api.paymentList({}, res => {
				if (res.status) {
					this.payments = this.formatPayments(res.data)
				}
			})
		},
		// 支付方式处理
		formatPayments(payments) {
			// 过滤支付宝支付
			payments = payments.filter(item => item.code !== 'alipay')

			// 如果是充值订单 过滤余额支付 过滤非线上支付方式
			if (this.type === 2) {
				payments = payments.filter(item => item.code !== 'balancepay' || item.is_online === 1)
			}

			// 设置logo图片
			payments.forEach(item => {
				this.$set(item, 'icon', '/static/image/' + item.code + '.png')
			})

			return payments
		},
		// 用户点击支付方式处理
		toPayHandler(e) {
			this.popShow = true;
			let code = e.target.value.code;
			let formId = e.detail.formId;

			let data = {
				payment_code: code,
				payment_type: this.type,
				params: {
					formid: formId
				}
			}
			data['ids'] = (this.type == 1 || this.type == 5 || this.type == 6) ? this.orderId : this.uid

			// 判断订单支付类型
			if (this.type == 2 && this.recharge) {
				data['params'] = {
					money: this.recharge
				}
			} else if ((this.type == 5 || this.type == 6) && this.recharge) {
				data['params'] = {
					formid: this.orderId
				}
			}
			let _this = this
			switch (code) {
				case 'wechatpay':
					this.$api.pay(data, res => {
						if (res.status) {
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: res.data.timeStamp,
								nonceStr: res.data.nonceStr,
								package: res.data.package,
								signType: res.data.signType,
								paySign: res.data.paySign,
								success: function(e) {
									if (e.errMsg === 'requestPayment:ok') {
										_this.$common.successToShow(res.msg, () => {
											_this.$common.redirectTo('/pages/goods/payment/result?id=' + res.data.payment_id)
										})
									}
								}
							});
						} else {
							this.$common.errorToShow(res.msg)
						}
					})
					break
				case 'balancepay':
					/**
					 *  用户余额支付
					 * 
					 */
					this.$api.pay(data, res => {
						if (res.status) {
							this.$common.redirectTo('/pages/goods/payment/result?id=' + res.data.payment_id)
						} else {
							this.$common.errorToShow(res.msg)
						}
					})
					break
				case 'offline':
					/**
					 * 线下支付
					 */
					this.$common.modelShow('线下支付说明', '请联系客服进行线下支付', () => {}, false, '取消', '确定')
					break
			}

		},
		// 支付中显示隐藏
		popBtn() {
			this.popShow = false
		}
	}

}
